import axios from 'axios'
import router from '@/router'

const ajax = axios.create({
    // headers:{
    //     Authorization : 'JWT ' + localStorage.getItem('token'),
    // }
})

ajax.interceptors.request.use(config => {
    config.headers['Authorization'] = 'JWT ' + localStorage.getItem('backstagetoken')
    return config
  })
  
  ajax.interceptors.response.use(resp => {
    
    return resp
  },error=>{
      if(error.response.status == 401){
            localStorage.removeItem('backstagetoken')
            localStorage.removeItem("backstagepeople");
          router.push('/login')
          alert('用户身份失效，请重新登录')
      }
  })


var ipURL = 'https://wiki.mj.team:5000' //wiki.mj.team   https://192.168.68.131:5000   http://192.168.68.214:5000

export const getLogin = (username,password) => ajax.post(ipURL+'/admin/user/login',{
    username,password 
}) //登录请求

